var state = {
  users: [],
  selectedUser: {},
  teachers: []
};
var getters = {
  users: function users(state) {
    return state.Users;
  },
  selectedUser: function selectedUser(state) {
    return state.selectedUser;
  },
  teachers: function teachers(state) {
    return state.teachers;
  }
};
var mutations = {
  SetUsers: function SetUsers(state, payload) {
    state.users = payload;
  },
  SetSelectedUser: function SetSelectedUser(state, payload) {
    state.selectedUser = payload;
    console.log("state.selectedUser: ", state.selectedUser);
  },
  SetTeachers: function SetTeachers(state, payload) {
    state.teachers = payload;
  }
};
var actions = {};
export default {
  namespaced: true,
  state: state,
  getters: getters,
  mutations: mutations,
  actions: actions
};